import { Pipe, PipeTransform } from '@angular/core';
import { IRestaurantMenuData } from '../interfaces/interfaces';

@Pipe({
  name: 'filtro'
})
export class FiltroPipe implements PipeTransform {

  transform(arreglo: IRestaurantMenuData[], typeShow: string= '', columna: string = 'type'): IRestaurantMenuData[] {
    //El arreglo se consulta 2 veces, la primera vacío y la segunda con datos
    // por eso contemplamos la opción de que nos venga vacío y no de fallo
    if ( !arreglo ) {
      return arreglo;
    }

    return arreglo.filter(
      item => item[columna].toLocaleLowerCase().includes(typeShow)
    );
  }

}
