import { Extra } from './extra';

export class OrderLine {
    idOrder: number;
    idLine: number;
    idProduct: number;
    codeExtrasAndAllergy: string;
    nameProduct: string;
    extras?: Extra[];
    amount: number;
    price: number;

    constructor( _idOrder: number, _idLine: number, _idProduct: number, _codeExtrasAndAllergy: string,
                 _nameProduct: string, _extras: Extra[], _amount: number, _price: number ){
        this.idOrder = _idOrder;
        this.idLine = _idLine;
        this.idProduct = _idProduct;
        this.nameProduct = _nameProduct;
        this.codeExtrasAndAllergy = _codeExtrasAndAllergy;
        this.extras = _extras;
        this.amount = _amount;
        this.price = _price;
    }
}
