import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { IRestaurantMenuData, ICategory } from '../interfaces/interfaces';

@Injectable({
  providedIn: 'root'
})
export class DataService {

  constructor( private http: HttpClient) { }

  getRestaurantMenu(){
    return this.http.get<IRestaurantMenuData[]>('/assets/data/restaurant-menu.json');
  }

  getRestaurantMenuCategory(){
    return this.http.get<ICategory[]>('/assets/data/restaurant-menu-categories.json');
  }
}
